@charset "utf-8";
@import "settings";
.box-intro {
    text-align: center;
    padding: 2em 0 0;
    position: relative;
    @include clearfix;
    &:before {
        content: "";
        background: #F7F8FA;
        @include cV(height, 210px, 390px, 390px);
        width: 100%;
        position: absolute;
        z-index: -1;
        top: 0;
        left: 0;
    }
    &:after {
        content: "";
        height: 0;
        width: 0;
        position: absolute;
        z-index: -1;
        @include cV(top, 209px, 389px, 389px);
        left: 50%;
        @include transform(translate(-50%, 0));
        border-style: solid;
        border-width: 610px 2000px 0 0;
        border-color: #F7F8FA transparent transparent transparent;
    }
    h2 {
        span {
            display: block;
            @include fsz(16);
            font-weight: 900;
        }
    }
    .title-header {
        @include fsz(38);
        color: $primary;
        font-weight: 800;
        display: flex;
        align-items: center;
        justify-content: center;
        em {
            font-size: 70%;
        }
        @include mq(pc) {
            @include fsz(80);
            background: url(../images/img-pc.png) no-repeat left center;
            padding-left: 145px;
            margin: 15px 0;
        }
    }
    .sub-title {
        font-weight: 700;
        @include fsz(20);
        @include mq(pc) {
            &.sub-title-mt {
                margin-bottom: -40px;
                margin-top: 40px;
                padding-left: 20%;
                font-weight: 900;
                @include fsz(30);
            }
        }
    }
    .img-intro {
        position: relative;
        .sticker {
            position: absolute;
            width: 10em;
            height: 10em;
            background: $primary;
            color: $white;
            right: 0;
            top: 0;
            @include border-radius(50%);
            @include mq(pc) {
                top: 40%;
                @include transform(translate(0, -40%));
                width: 195px;
                height: 195px;
            }
            .inner {
                display: flex;
                height: 100%;
                flex-direction: column;
                justify-content: center;
                .title {
                    @include fsz(11);
                    font-weight: bold;
                    @include mq(pc) {
                        font-size: 17px;
                    }
                }
                .title-main {
                    @include fsz(40);
                    font-weight: 900;
                    line-height: 1.3;
                    @include mq(pc) {
                        font-size: 63px;
                    }
                    span {
                        @include fsz(26, 40);
                    }
                }
                .title-sub {
                    font-weight: bold;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    @include mq(pc) {
                        font-size: 17px;
                    }
                    @include mq(tab_sp) {
                        img {
                            max-width: 22px;
                        }
                    }
                }
            }
        }
    }
    .text-btn {
        @include fsz(15);
        font-weight: bold;
        .fa {
            margin-left: .5em;
            color: $primary;
        }
    }
    .container {
        @include mq(pc) {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 2em 0 0;
            .box-left-intro {
                width: 50%;
                padding-left: 2%;
            }
        }
        @include mq(sp) {
        	position: relative;
        	padding-bottom: 3em;
        	margin-bottom: 1em;
        	&:before{
        		content: "";
        		width: 135px;
        		height: 2px;
        		background: #000;
        		position: absolute;
        		left: 50%;
        		bottom: 0;
        		@include transform(translate(-50%,0));
        		z-index: 1;
        	}
        }
    }
    .box-btn{
    	.btn{
    		@include mq(pc) {
    			max-width: 360px;
    		}
    	}
    }
}
@include mq(sp) {
	.box-btn .btn{
		width: 90%;
	}	
}

/* -----------------------------------------------------------
	#box-let-it-now
----------------------------------------------------------- */

.box-let-it-now {
    @include clearfix;
    .title-desc {
        text-align: center;
        @include fsz(12);
        color: $menu;
        @include cV(margin,20px 0,20px 0,.5em 0 1.5em);
    }
    .text {
        text-align: center;
        @include fsz(20);
        font-weight: 800;
    }
    .box-btn{
    	text-align: center;
    	@include cV(margin,20px auto,20px auto,1em auto 0);
    	@include mq(pc) {
	    	.btn{
	    		min-width: 360px;
	    		margin: auto;
	    	}
    	}
    }
}

.box-parrent-category {
    @include clearfix;
    margin-bottom: 20px;
    @include mq(sp) {
    	overflow-x: auto;
    }
    ul {
        @include clearfix;
        @include mq(sp) {
            display: flex;
            align-items: center;
            min-width: 2095px;
        }
        li {
            float: left;
            margin-right: 5px;
            margin-bottom: 10px;
            a {
                display: inline-block;
                background: $bg-category;
                padding: 4px 13px;
                @include border-radius(2em);
                @include fsz(13);
                &.active{
                	background: $primary;
                	color: $white;
                }
            }
        }
    }
}

.box-sub-category {
    @include clearfix;
    margin-bottom: 20px;
    ul {
        @include clearfix;
        text-align: center;
        li {
            margin-right: 5px;
            margin-bottom: 5px;
            display: inline-block;
            padding: 0 7px;
            a {
                display: inline-block;
                padding: 5px 5px;
                @include fsz(15);
                color: $menu;
                &.active{
                	border-bottom: 1px solid $primary;
                	color: $baseColor;
                	font-weight: 700;
                }
            }
        }
    }
}

.box-nominate-a-cast{
	@include clearfix;
	h3.title-nominate{
		text-align: center;
		@include fsz(20);
		margin: 15px 0;
	}
}